* {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  font-size: 112.5%;
  font-family: 'Roboto', sans-serif;
  background: linear-gradient(
    18deg,
    rgba(2, 0, 36, 1) 4%,
    rgba(1, 76, 115, 1) 33%,
    rgba(1, 95, 125, 1) 49%,
    rgba(1, 122, 139, 1) 57%,
    rgba(1, 137, 146, 1) 62%,
    rgba(0, 255, 207, 1) 87%
  );
  background-attachment: fixed;
  background-repeat: no-repeat;
}

.light__button {
  background-color: #8ff2ee;
  display: inline-block;
  padding: 10px 25px 10px 25px;
  margin: auto;
  border-radius: 7px;
  font-size: 80%;
  cursor: pointer;
}

.night__button {
  background-color: #8ff2ee;
  display: inline-block;
  padding: 10px 25px 10px 25px;
  margin: auto;
  border-radius: 7px;
  font-size: 80%;
  cursor: pointer;
}

.light-mode {
  background: white;
  content: 'Hide answer';
}

main {
  margin: 30px;
  overflow: scroll;
  flex: 1 0 auto;
  height: 100%;
}

.displaynone {
  display: none;
}
